.session-links-wrapper {
    color: $white-4;
    font-weight: bold;
    padding-right: 56px;
    display: flex;
    flex-flow: row wrap;
}

.session-links-wrapper div {
    border: 1px solid $white-4;
    padding: 5px 15px;
}

.session-links-wrapper div:hover {
    background: $black-3;
    cursor: pointer;
}

.session-links-wrapper div:hover a, .session-links-wrapper div:hover svg {
    color: $white-2;
    fill: $white-2;
}

.session-links-wrapper a {
    font-size: 1.2em;
    position: relative;
    width: 100%;
    height: 100%;
}

.session-links-wrapper__login-wrapper svg {
    margin-right: 5px;
}